q:before,
q:after {
  content: ''; }

abbr,
acronym {
  border: 0;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal; }

/* to preserve line-height and selector appearance */
sup {
  vertical-align: text-top; }

sub {
  vertical-align: text-bottom; }

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
