body {
  background-image: url(data:image/png;base64,);
  font-family: 'Comfortaa', cursive; }

.component {
  /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  height: calc(100vh);
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100); }

.template {
  height: calc(100% - 90px);
  max-width: 1600px;
  margin: 0 auto;
  position: relative;
  overflow: hidden; }

.actions {
  height: 90px;
  border-top: 1px dashed silver;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 15px; }
  .actions__input-count, .actions__input-size {
    width: 100%;
    max-width: 500px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: relative;
    top: 3px; }
  .actions__input-count {
    margin-right: 20px; }
  .actions__input-size {
    margin-left: 20px; }
  .actions__text-count, .actions__text-size {
    text-align: center;
    font-size: 13px;
    position: relative;
    top: 3px;
    display: flex;
    justify-content: space-between;
    margin-left: -4px;
    margin-right: -7px; }

.dot {
  height: 20px;
  width: 20px;
  background: #000;
  border-radius: 50%;
  will-change: transform;
  -webkit-transition: .7s transform ease, .35s width ease, .35s height ease, .65s background-color linear;
  transition: .7s transform ease, .35s width ease, .35s height ease, .65s background-color linear;
  position: absolute; }

.device-info {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #eff8fa;
  text-align: center;
  display: none;
  align-items: center;
  padding: 0 5px;
  z-index: 1; }

.btn {
  cursor: pointer;
  min-width: 140px; }

.rc-slider {
  padding: 10px 0;
  cursor: pointer; }
  .rc-slider-step {
    background: #007bff; }
  .rc-slider-handle {
    width: 20px;
    height: 20px;
    margin-top: -8px; }
    .rc-slider-handle, .rc-slider-handle:active, .rc-slider-handle:hover, .rc-slider-handle:focus {
      border-color: #007bff; }
    .rc-slider-handle:active, .rc-slider-handle:focus {
      box-shadow: 0 0 5px #007bff; }

@media screen and (max-width: 900px) {
  .template {
    height: calc(100% - 70px); }
  .actions {
    height: 70px; } }

@media screen and (max-width: 750px) {
  .template {
    height: calc(100% - 65px); }
  .actions {
    height: 65px; } }

@media screen and (max-width: 600px) {
  .template {
    height: calc(100% - 60px); }
  .actions {
    height: 60px; } }

@media screen and (max-width: 600px) {
  .btn {
    font-size: 14px; } }

@media screen and (max-width: 900px) {
  .actions {
    padding-bottom: 8px; } }

@media screen and (orientation: portrait) {
  .device-info {
    display: flex;
    justify-content: center; } }
